<template>
  
    <div class="container articles">
      
      <div class="row justify-content-center">
        <div class="col-12 col-md-10 col-lg-8" id="allArticles">
          <a
            href=""
            data-toggle="modal"
            data-target="#modalAddArticle"
            class="my-2 btn btn-sm btn-block btn-success"
            >Poster un article...</a
          >
          <div
            class="modal fade"
            id="modalAddArticle"
            tabindex="-1"
            aria-labelledby="modalAddArticle"
            aria-hidden="true"
          >
            <div class="modal-dialog">
              <div class="modal-content">
                <form enctype="multipart/form-data">
                  <div class="modal-header">
                    <p class="modal-title h5">Poster un nouvel article</p>
                  </div>
                  <div class="row modal-body">
                    <div class="col-12 justify-content-center form-group">
                      <label for="newArticleTitle" class="sr-only"
                        >Titre :</label
                      >
                      <textarea
                        class="form-control"
                        v-model="newArticleTitle"
                        id="newArticleTitle"
                        name="articleTitle"
                        rows="2"
                        placeholder="Votre titre ici..."
                      ></textarea>
                      <label for="newArticleChapo" class="sr-only"
                        >Sous-titre :</label
                      >
                      <textarea
                        class="form-control"
                        v-model="newArticleChapo"
                        id="newArticleChapo"
                        name="articleChapo"
                        rows="4"
                        placeholder="Votre sous-titre ici..."
                      ></textarea>
                      <label for="newArticle" class="sr-only">Article :</label>
                      <textarea
                        class="form-control"
                        v-model="newArticle"
                        id="newArticle"
                        name="article"
                        rows="10"
                        placeholder="Votre article ici..."
                      ></textarea>
                    </div>
                    <div class="col-12 justify-content-center text-center">
                      <img :src="newImage" class="w-50 rounded" />
                      <p class="small text-center">Image à partager</p>
                    </div>
                    <div class="col-12 justify-content-center">
                      <div class="form-group justify-content-center">
                        <label for="File" class="sr-only"
                          >Choisir une nouvelle photo</label
                        >
                        <input
                          @change="onFileChange()"
                          type="file"
                          ref="file"
                          name="image"
                          class="form-control-file"
                          id="File"
                          accept=".jpg, .jpeg, .gif, .png"
                        />
                      </div>
                    </div>
                  </div>
                  <div class="modal-footer">
                    <div class="row w-100 justify-content-spacebetween">
                      <div class="col-6">
                        <a
                          data-dismiss="modal"
                          class="btn btn-secondary btn-block"
                          >Annuler</a
                        >
                      </div>
                      <div class="col-6">
                        <button
                          type="submit"
                          @click.prevent="addNewArticle()"
                          class="btn btn-success btn-block"
                        >
                          Valider
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
          <div
            v-for="article in articles"
            :key="article.id"
            class="card bg-light my-3"
          >
            <div
              class="card-header bg-light d-flex align-items-center justify-content-between m-0 p-1"
            >
              <div>
                <img
                  :src="article.avatar"
                  alt="image de profil"
                  height="40"
                  class="m-0 rounded-circle"
                />
                <span class="small text-dark m-0 p-1">
                  Posté par {{ article.userName }}
                  <span v-if="!article.isActive" class="small text-danger"
                    >(supprimé)</span
                  >, le
                  {{
                    article.createdAt
                      .slice(0, 10)
                      .split("-")
                      .reverse()
                      .join("/") +
                      " à " +
                      article.createdAt.slice(11, 16)
                  }}
                </span>
              </div>
              <div
                v-if="
                  article.UserId == this.currentUserId || this.isAdmin == 'true'
                "
              >
                <a :href="'#/article/edit/' + article.id"
                  ><img
                    src="/images/edit.svg"
                    class="m-1 p-0"
                    alt="Editer l'article'"
                    title="Editer l'article'"
                /></a>
                <a :href="'#/article/drop/' + article.id"
                  ><img
                    src="/images/drop.svg"
                    class="m-1 p-0"
                    alt="Supprimer l'article'"
                    title="Supprimer l'article'"
                /></a>
              </div>
            </div>
            <div class="card-body text-dark text-left">
              <h4 v-if="article.articleTitle !== ''">
                {{ article.articleTitle }}
              </h4>
              <p v-if="article.articleChapo !== ''">
                <b>{{ article.articleChapo }}</b>
              </p>
              <p class="small" v-if="article.article !== ''">
                {{ article.article }}
              </p>
              <img
                class="w-100"
                :src="article.articleUrl"
                alt="image de l'article"
                v-if="article.articleUrl !== ''"
              />
            </div>
          </div >
          <NoArticle v-if="noArticle" class="noArticle"></NoArticle>
          <div class="card bg-danger">
            <p class="my-2 text-center text-white">
              Il n'y a pas d'articles plus ancien que celui au-dessus...
            </p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import NoArticle from "./NoArticle";
import axios from "axios";
import Swal from "sweetalert2";


export default {
  name: "Articles",
  components: {
    NoArticle,

  },
  data() {
    return {
      noArticle: false,
      isAdmin: false,
      isActive: true,
      newImage: "",
      currentUserId: "",
      newArticleTitle: "",
      newArticleChapo: "",
      newArticle: "",
      file: null,
      articles: [],
    };
  },
  methods: {
    onFileChange() {
      this.file = this.$refs.file.files[0];
      this.newImage = URL.createObjectURL(this.file);
    },
    addNewArticle() {
      const formData = new FormData();
      formData.set("image", this.file)
      formData.set("UserId", this.currentUserId.toString())
      formData.set("articleTitle", this.newArticleTitle.toString())
      formData.set("articleChapo", this.newArticleChapo.toString())
      formData.set("article", this.newArticle.toString())
      axios
        .post("http://127.0.0.1:3000/api/articles/", formData, {
          headers: { Authorization: " Bearer " + localStorage.getItem("token") },
        })
        .then(() => {
          this.UserId = "";
          this.newArticleTitle = "";
          this.newArticleChapo = "";
          this.newArticle = "";
          this.file = null;
          Swal.fire({
            text: "Article posté !",
            footer: "Redirection en cours...",
            icon: "success",
            timer: 2000,
            showConfirmButton: false,
            timerProgressBar: true,
            willClose: () => {
              location.reload();
            },
          });
        })
        .catch((error) => {
          const codeError = error.message.split("code ")[1];
          let messageError = "";
          switch (codeError) {
            case "400":
              messageError = "L'article n'a pas été posté !";
              break;
            case "401":
              messageError = "Requête non-authentifiée !";
              break;
          }
          Swal.fire({
            title: "Une erreur est survenue",
            text: messageError || error.message,
            icon: "error",
            timer: 3500,
            showConfirmButton: false,
            timerProgressBar: true,
          });
        });
    },
  },
  created: function() {
    this.isAdmin = localStorage.getItem("role");
    this.currentUserId = localStorage.getItem("userId");
    if (localStorage.getItem("refresh") === null) {
      localStorage.setItem("refresh", 0);
      location.reload();
    }
    axios
      .get("http://127.0.0.1:3000/api/articles", {
        headers: { Authorization: "Bearer " + localStorage.getItem("token") },
      })
      .then((res) => {
        const rep = res.data.ListeArticles;
        if (rep.length === 0) {
          this.noArticle = true;
        } else {
          this.noArticle = false;
        }
        this.articles = rep;
      })
      .catch((error) => {
        const codeError = error.message.split("code ")[1];
        let messageError = "";
        switch (codeError) {
          case "400":
            messageError = "La liste des articles n'a pas été récupérer !";
            break;
        }
        Swal.fire({
          title: "Une erreur est survenue",
          text: messageError || error.message,
          icon: "error",
          timer: 3500,
          showConfirmButton: false,
          timerProgressBar: true,
        });
      });
  },
};
</script>

<style lang="scss">

.articles{
  padding-top: 15em;
}

</style>
